<template>
    <div>
        <h3>Trier par:</h3>
        <select name="dropdownSorting" v-model="selected" @change="sortHandler()">
            <option disabled hidden value="">Trier par: Par domaine</option>
            <option value="domain">Par domaine</option>
            <option value="job">Par poste</option>
        </select>
        <h3>Afficher seulement:</h3>
        <ul v-for="typ in types" v-bind:key="typ">
            <sorting-item :type='typ'></sorting-item>
        </ul>
    </div>
</template>

<script>
    import types from './../../utility/types.JSON';
    import SortingItem from './sorting-item.vue';

    export default {
        data () {
            return {
                types: types.types,
                selected : "domain",
            }
        },
        methods : {
            sortHandler() {
                this.$root.$refs.domainList.filterOption = this.selected;
            }
        },
        components: {
            SortingItem,
        }
    }
</script>

<style>
h3 {
    margin-top: 40px;
}
select {
    width: 50%;
    height: auto;
    border: 1px solid var(--primary);
    margin-top: 10px;
    margin-bottom: 10px;
    padding: 5px;
}
</style>