<template>
    <div class="container">
        <h3>Intérêts :</h3>
        <div id="selectedInterestsBox">
            <ul>
                <p v-if="selectedInterests.length == 0">Aucun intérêt sélectionné</p>
                <li v-for="interest in selectedInterests" v-bind:key="interest.id">
                    <interest-item :interest='interest'></interest-item>
                    <img src="./../../assets/Icon_x.svg" @click="removeInterest(interest)">
                </li>
            </ul>
        </div>
        <select name="dropdownInterests" v-model="selected" @change="interestHandler()">
            <option disabled hidden value="">Sélectionner vos centres d'intérêts</option>
            <option :value="interest" v-for="interest in interests" v-bind:key="interest"> 
                {{interest}}
            </option>
        </select>
    </div>
</template>

<script>
    import interests from './../../utility/interests.JSON';
    import interestItem from './interest-item.vue';

    export default {
        name: "interests",
        data() {
            return {
                selectedInterests: [],
                selected : "",
                interests: interests.interests
            }
        },
        components: {
            interestItem
        },
        methods : {
            interestHandler() {
                this.interests = this.interests.filter(interest => interest != this.selected);
                this.addInterest(this.selected);
            },
            addInterest(interest) {
                this.selectedInterests.push(interest);
                this.selected = "";
                this.$root.$refs.domainList.updateInterests(this.selectedInterests);
            },
            removeInterest(interest) {
                this.selectedInterests = this.selectedInterests.filter(data => data != interest);
                this.interests.push(interest);
                this.$root.$refs.domainList.updateInterests(this.selectedInterests);
            }
        }
    };
</script>

<style scoped>

    h3 {
        margin-top: 40px;
    }

    #selectedInterestsBox {
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        padding: 10px;
        margin-top: 10px;
        border: 1px solid var(--primary);
        width: 100%;
    }

    select {
        width: 100%;
        height: auto;
        border: 1px solid var(--primary);
        margin-top: 10px;
        margin-bottom: 10px;
        padding: 5px;
    }

    ul {
        display: flex;
        flex-wrap: wrap;
    }

    li {
        text-align: left;
        padding: 4px;
        margin: 4px 4px;
        list-style: none;
        display: flex;
        flex-direction: row;
        align-items: center;
        background-color: lightgray;
        border-radius: 4px;
    }
    
    img {
        width: 20px;
        height: 20px;
        margin-left: 10px;
    }

    img:hover {
        cursor: pointer;
        filter : invert(9%) sepia(92%) saturate(6905%) hue-rotate(360deg) brightness(87%) contrast(108%);
    }


</style>