<template>
    <div class="condition">
        <input type="checkbox" class="checkbox" v-model="checked" @click="conditionChecked()">
        <label>{{ condition }}</label>
    </div>
</template>

<script>


export default {
    name: "Condition",
    data() {
        return {
            checked: false,
        }
    },
    methods: {
        conditionChecked() {
            if (!this.checked) {
                this.$root.$refs.conditionList.addCondition(this.condition);
            } else {
                this.$root.$refs.conditionList.removeCondition(this.condition);
            }
        }
    },
    props :{
        condition : String,
    }
};
</script>

<style scoped>
.condition {
    display: flex;
}
#checkbox {
    width: 15px;
    height: 15px;
    margin-right: 10px;
    min-width: 15px;
    border-radius: 0%;
}
label {
    font-size: 0.9em;
}

</style>
