<template>
    <div class="sorting">
        <input type="checkbox" class="checkbox" v-model="checked" @click="sortChecked()">
        <label>{{ type }}</label>
    </div>
</template>

<script>


export default {
    name: "Sortihng",
    data() {
        return {
            checked: false,
        }
    },
    methods: {
        sortChecked() {
            if (!this.checked) {
                this.$root.$refs.conditionList.addType(this.type);
            } else {
                this.$root.$refs.conditionList.removeType(this.type);
            }
        }
    },
    props :{
        type : String,
    }
};
</script>

<style scoped>
.sorting {
    display: flex;
}
#checkbox {
    width: 15px;
    height: 15px;
    margin-right: 10px;
    min-width: 15px;
    border-radius: 0%;
}
label {
    font-size: 0.9em;
}

</style>
